angular.module('stmApp')
.controller('MusicController', MusicController);

MusicController.$inject = ['$state'];
function MusicController($state) {
  const music = this;
  this.playPauseCounter = 0;
  this.songCounter = 1;
  this.currentSong = new Audio();
  this.songList = [{
    title: 'OnE',
    src: 'one',
    trackNo: 1
    },{
    title: 'WAITInG FOr YOU',
    src: 'w4u',
    trackNo: 2
    }, {
    title: 'BABY, THESE FAnGS ArE POISOn',
    src: 'fangs',
    trackNo: 3
    }, {
    title: 'SMILE',
    src: 'smile',
    trackNo: 4
    }, {
    title: 'Y3H!',
    src: 'y3h',
    trackNo: 5
    }, {
    title: 'BAMBOO rIDGE',
    src: 'bamboo',
    trackNo: 6
    }
  ];
  function click() {
    console.log(this);
  }
  function playSong(track, autoplay) {
    if (!this.currentSong.src) {
      this.currentSong.src = `../../audio/one.mp3`;
    }
    if (track) {
      if (autoplay) {
        this.playPauseCounter = 1;
      }
      this.currentSong.pause();
      this.currentSong.src = null;
      this.currentSong.src = `../../audio/${track}.mp3`;
      if (this.playPauseCounter % 2 !== 0) {
      this.currentSong.play();
    }
    }
    else if (!track) {
      this.playPauseCounter ++;
      if (this.playPauseCounter % 2 === 0) {
        this.currentSong.pause();
      }
      else {
        this.currentSong.play();
      }
    }
  }
  function trackChange(direction) {
    if (direction === "next" && this.songCounter < 6) {
      this.songCounter ++;
      this.chooseSong();
    }
    else if (direction === "previous" && this.songCounter > 1) {
      this.songCounter --;
      this.chooseSong();
    }
  }
  function chooseSong() {
    switch (this.songCounter) {
      case 1 : this.playSong('one');
      break;
      case 2 : this.playSong('w4u');
      break;
      case 3 : this.playSong('fangs');
      break;
      case 4 : this.playSong('smile');
      break;
      case 5 : this.playSong('y3h');
      break;
      case 6 : this.playSong('bamboo');
      break;
    }
  }
  this.playSong = playSong;
  this.trackChange = trackChange;
  this.chooseSong = chooseSong;
  console.log(music, $state);
}
